:root {
  font-size: 16px; }

body {
  background: white;
  font-family: "Josefin Sans", sans-serif; }

a {
  text-decoration: none;
  color: #fd8a80;
  font-size: 1em; }

ul {
  list-style-type: none;
  padding: 0;
  margin: 0; }

figure {
  margin: 0; }

p {
  line-height: 1.5; }

.logo {
  background: #fffcb6;
  border-radius: 50%;
  padding-top: 10px;
  box-sizing: border-box;
  height: 100px;
  width: 100px;
  overflow: hidden; }

nav.main-nav li {
  display: inline-block;
  padding: 10px 20px; }
  nav.main-nav li a {
    font-size: 1.1em;
    font-weight: 700;
    letter-spacing: .8px;
    color: #566270;
    position: relative; }
    nav.main-nav li a:before {
      content: '';
      display: none;
      width: 100%;
      background: #fffcb6;
      height: 4px;
      position: absolute;
      top: .65em;
      z-index: -1; }
    nav.main-nav li a:hover {
      color: gray; }
      nav.main-nav li a:hover:before {
        display: inline-block; }

header.main-header {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto; }
  header.main-header::after {
    clear: both;
    content: "";
    display: block; }
  header.main-header .top-header {
    position: relative; }
  header.main-header nav.main-nav {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    transform: translateY(-50%);
    right: 0; }

section.home-intro {
  height: calc(100vh - 100px);
  position: relative; }
  section.home-intro .intro-wrap {
    width: 100%;
    max-width: 720px;
    margin-left: auto;
    margin-right: auto;
    position: absolute;
    top: 45%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%); }
    section.home-intro .intro-wrap::after {
      clear: both;
      content: "";
      display: block; }
  section.home-intro figure.myAvtar {
    text-align: center;
    float: left;
    display: block;
    margin-right: 2.35765%;
    width: 31.76157%; }
    section.home-intro figure.myAvtar:last-child {
      margin-right: 0; }
    @media screen and (max-width: 500px) {
      section.home-intro figure.myAvtar {
        width: 100%; } }
    section.home-intro figure.myAvtar img {
      width: 100%;
      max-width: 250px; }
  section.home-intro .intro-content {
    float: left;
    display: block;
    margin-right: 2.35765%;
    width: 65.88078%; }
    section.home-intro .intro-content:last-child {
      margin-right: 0; }
    @media screen and (max-width: 500px) {
      section.home-intro .intro-content {
        width: 100%; } }
  section.home-intro button {
    border: 1px solid #076fe4;
    border-radius: 3px;
    box-shadow: inset 0 1px 0 0 #8ebcf1;
    color: white;
    display: inline-block;
    font-size: inherit;
    font-weight: bold;
    background-color: #4294f0;
    background-image: -webkit-linear-gradient(#4294f0, #0776f3);
    background-image: linear-gradient(#4294f0, #0776f3);
    padding: 7px 18px;
    text-decoration: none;
    text-shadow: 0 1px 0 #0065d6;
    background-clip: padding-box; }
    section.home-intro button:hover:not(:disabled) {
      background-color: #2f87ea;
      background-image: -webkit-linear-gradient(#2f87ea, #086fe3);
      background-image: linear-gradient(#2f87ea, #086fe3);
      box-shadow: inset 0 1px 0 0 #60a2ec;
      cursor: pointer; }
    section.home-intro button:active:not(:disabled), section.home-intro button:focus:not(:disabled) {
      border: 1px solid #076fe4;
      box-shadow: inset 0 0 8px 4px #0868d3, inset 0 0 8px 4px #0868d3; }
    section.home-intro button:disabled {
      cursor: not-allowed;
      opacity: 0.5; }
